import Estpa7 from './pagina7.module.css'
import Encabezado from '../componentes/encabezado';
import cal1 from '../assets/img/cal1.jpg';
import cal2 from '../assets/img/cal2.jpg';

import ControlledCarousel from "../componentes/carrusel";

export const Pagina7 =() => {
    return (
        <div className={Estpa7.Pag7}>
            <Encabezado titulo="CALAPURCA ORUREÑO"/>
            <body className={Estpa7.cuerpo}>
            PLATO  ANDINO DE ORURO 
            <div className={Estpa7.contsec}>
            <div className={Estpa7.imagenes}>
                    <p>En las vibrantes tierras altas de Bolivia, se sirve un plato que encarna la riqueza cultural y gastronómica de la región: el "Sajta de Pollo". Este exquisito manjar combina la suavidad del pollo con la intensidad de los sabores locales. El pollo se cocina lentamente en una mezcla de papas, pimientos y especias autóctonas, creando una armonía de texturas y aromas que deleitan los sentidos. Acompañado de arroz y adornado con cilantro fresco, el Sajta de Pollo no solo es un festín para el paladar, sino también un viaje culinario que revela la diversidad y autenticidad de la cocina boliviana. Cada bocado cuenta una historia de tradición y pasión, invitando a todos a disfrutar de la riqueza de este país andino. ¡Buen provecho!</p>
                </div>
                <section className={Estpa7.seccion}>
                <img src={cal1}
                        width="100%"
                        height="100%"/>
                </section>
                <div className={Estpa7.imagenes}>
                <p> En las vibrantes tierras altas de Bolivia, se sirve un plato que encarna la riqueza cultural y gastronómica de la región: el "Sajta de Pollo". Este exquisito manjar combina la suavidad del pollo con la intensidad de los sabores locales. El pollo se cocina lentamente en una mezcla de papas, pimientos y especias autóctonas, creando una armonía de texturas y aromas que deleitan los sentidos. Acompañado de arroz y adornado con cilantro fresco, el Sajta de Pollo no solo es un festín para el paladar, sino también un viaje culinario que revela la diversidad y autenticidad de la cocina boliviana. Cada bocado cuenta una historia de tradición y pasión, invitando a todos a disfrutar de la riqueza de este país andino. ¡Buen provecho!</p>
                </div>
                <section className={Estpa7.seccion}>
                <img src={cal2}
                        width="100%"
                        height="100%"/>
                </section>
            </div>
            <div className={Estpa7.platos}>
                platos populares
            </div>
            <ControlledCarousel/>   
            </body>
        </div>
    );
}