import Estplant from './plantilla.module.css'
import salar from '../assets/img/salar.jpeg'
import casera from '../assets/img/caseras.jpg'
import corazon from '../assets/img/chuima.jpg'
export const Plantilla =() => {
    return (
    <div className="Plant">
        <div className={Estplant.todo}>
            <body className={Estplant.cuerpo}>
                Cuerpo
                <div className={Estplant.contsec}>
                    <section class={Estplant.seccion}>
                    <img src={salar}
                        width="100%"
                        height="100%"
                        />
                    <p>
                        
El Salar de Uyuni, en Bolivia, es el salar más grande del mundo y un tesoro natural impresionante. Este vasto desierto blanco, compuesto por sal cristalizada, crea un paisaje surrealista que parece fusionar el cielo y la tierra. Durante la temporada de lluvias, se convierte en un enorme espejo reflectante, creando una ilusión óptica única y cautivadora. Este fenómeno natural y su inmensa extensión hacen del Salar de Uyuni un destino verdaderamente asombroso en el corazón de América del Sur.
                    </p>

                    </section>
                    <div className={Estplant.imagen}>
                    <img src={casera}
                        width="100%"
                        height="100%"
                        />
                    </div>
                </div>
                <div className={Estplant.contsec}>
                    <div className={Estplant.imagen}>
                    <img src={corazon}
                        width="100%"
                        height="100%"
                        />
                    </div>
                    <section className={Estplant.seccion}>
                    seccion 2
                    <p>
Bolivia, un país situado en el corazón de América del Sur, se distingue por su rica diversidad geográfica, cultural y étnica. Con una mezcla de altiplanos andinos, selvas tropicales en la región del Amazonas y vastas llanuras, Bolivia alberga una variedad de paisajes impresionantes que van desde montañas imponentes hasta exuberantes bosques tropicales.
                    </p>
                    </section>
                </div>
            </body>
                
      
        </div>
      

    </div>


  );
}




