import Estpa4 from './pagina4.module.css'
import Encabezado from '../componentes/encabezado';
import pique1 from '../assets/img/pique1.jpg'
import pique2 from '../assets/img/pique2.jpg'
import pique3 from '../assets/img/pique3.jpg'



export const Pagina4 =() => {
    return (
        <div className={Estpa4.Pag4}>
            <Encabezado titulo="PIQUE MACHO"/>
            <body className={Estpa4.cuerpo}>
           
                <div className={Estpa4.contsec}>
                    <section className={Estpa4.seccion}>
                    <img src={pique2}
                        width="100%"
                        height="100%"/>
                    </section>
                    <div className={Estpa4.video}>
                    <img src={pique3}
                        width="100%"
                        height="100%"/>
                    </div>
                    <section className={Estpa4.seccion}>
                    <img src={pique1}
                        width="100%"
                        height="100%"/>
                    </section>
                </div>
                <div className={Estpa4.comenta}>
                  ////////////////
                </div>
            </body>
        </div>
    );
}