import Estpa2 from './pagina2.module.css'
import silpancho from'../assets/img/silpancho.jpg'
import asado from'../assets/img/asado.jpg'
import plato from'../assets/img/plato.jpg'
import asadito from'../assets/img/asdito.jpg'
import rostro from'../assets/img/Rostro_Asado_Orureño.jpg'



import Encabezado from "../componentes/encabezado"
export const Pagina2 =() => {
    return (
        <div className={Estpa2.Pag2}>
            <Encabezado titulo="ASADO"/>
            <body className={Estpa2.cuerpo}>
                <h1>Plato Orureño del altiplano</h1>
                <div className={Estpa2.contsec}>
                    <section className={Estpa2.seccion}>
                        <img src={asado}
                        width="100%"
                        height="100%"/>
                    </section>
                    <div className={Estpa2.imagen}>
                    <img src={plato}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa2.imagen}>
                    <img src={asadito}
                        width="100%"
                        height="100%"/>
                    </div>
                    <section className={Estpa2.seccion}>
                    <img src={rostro}
                        width="100%"
                        height="100%"/>
                    </section>
                </div>
                <div className={Estpa2.encuesta}>
                    Encuesta
                </div>
            </body>
        </div>
    );
}