import Encabezado from '../componentes/encabezado';
import Estpa9 from './pagina9.module.css'
import salteña from '../assets/img/salteña.jpg'
export const Pagina9 =() => {
    return (
        <div className={Estpa9.Pag9}>
            <Encabezado titulo="Pagina 9"/>
            <body className={Estpa9.cuerpo}>
                Intercambiar regalos
                <div className={Estpa9.contsec}>
                    <div className={Estpa9.imagen}>
                        <img src={salteña}/>
                    </div>
                    <section className={Estpa9.seccion}>
                        seccion 1
                    </section>
                </div>  
                <div className={Estpa9.regalo}>
                    comenta, el mejor regalo que diste y recibiste
                </div>
                <div className={Estpa9.contsec2}>  
                    <section className={Estpa9.seccion}>
                        seccion 2
                    </section>
                    <div className={Estpa9.imagen}>
                        imagen 2
                    </div>
                </div>
            </body>
        </div>
    );
}