import Esthom from './home.module.css'
import Encabezado from '../componentes/encabezado';
import RecatPlayer from 'react-player';
import Imagen  from '../assets/img/img2.jpg';
import bandera from '../assets/img/bandera.jpg';
export const Home =() => {
    const video="https://youtu.be/B6PGcNYLNDY"
    return (

        <div className={Esthom.Home}>
            <Encabezado titulo="PLATOS TIPICOS BOLIVIA"/>
            <body className={Esthom.cuerpo}>
                pagina1
                <div className={Esthom.vid}>
                    <RecatPlayer className="player"
                    url={video}
                    playing={false}
                    volumen={0.5}
                    width="100%"
                    height="100%"
                    
                    
                    />
                </div>
                <div className={Esthom.contsec}>
                    <section className={Esthom.seccion}>
                        <h2>seccion1</h2>
                        <img src={Imagen}
                        width="100%"
                        height="100%"
                        />
                    </section>
                    <section className={Esthom.seccion}>
                        <img src={bandera}
                        width="100%"
                        height="100%"/>
                    </section>
                </div>    
                <div className={Esthom.correo}>
                    rodrigocalledaza9@gmail.com
                </div>           
            </body>
        </div>
    );
}