import Estpa3 from './pagina3.module.css'
import Encabezado from '../componentes/encabezado';
import paseño1 from '../assets/img/paseño1.jpg'
import paseño2 from '../assets/img/paseño2.jpg'
import paseño3 from '../assets/img/paseño3.jpg'
import paseño4 from '../assets/img/paseño4.jpg'
import paseño5 from '../assets/img/paseño5.jpg'
import paseño6 from '../assets/img/paseño6.jpg'
export const Pagina3 =() => {
    return (
        <div className={Estpa3.Pag3}>
            <Encabezado titulo="Plato paseño"/>
            <body className={Estpa3.cuerpo}>
                
                <div className={Estpa3.contsec}>
                    <div className={Estpa3.imagen}>
                    <img src={paseño1}
                        width="100%"
                        height="100%"/>
                    </div>
                    <section className={Estpa3.seccion}>
                    <img src={paseño2}
                        width="100%"
                        height="100%"/>
                    </section>
                    <div className={Estpa3.imagen}>
                    <img src={paseño3}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa3.imagen}>
                    <img src={paseño4}
                        width="100%"
                        height="100%"/>
                    </div>
                    <section className={Estpa3.seccion}>
                    <img src={paseño5}
                        width="100%"
                        height="100%"/>
                    </section>
                    <div className={Estpa3.imagen}>
                    <img src={paseño6}
                        width="100%"
                        height="100%"/>
                    </div>
                </div>
            </body>
        </div>
    ) ;
}