import Estpa1 from './pagina1.module.css'
import Encabezado from '../componentes/encabezado';
import ControlledCarousel from "../componentes/carrusel";
import silpancho from'../assets/img/silpancho.jpg'
import asado from'../assets/img/asado.jpg'
import casera from'../assets/img/caseras.jpg'
import chuima from'../assets/img/chuima.jpg'
import pique from'../assets/img/pique.jpg'
import Form from 'react-bootstrap/Form';
export const Pagina1 =() => {
    return( 
    <div className={Estpa1.Pag1}>
        <Encabezado titulo="PRESENTACION"/>
        <body className={Estpa1.cuerpo}>
            visitar / ver el como la ciudad es/ta decorada
            <ControlledCarousel/>

            <div className={Estpa1.contsec}>
                <section className={Estpa1.seccion}>
                <img src={silpancho}
                        width="100%"
                        height="100%"/>
                </section>
                <div className={Estpa1.imagenes}>
                    <div className={Estpa1.desc}>
                    <img src={silpancho}
                        width="100%"
                        height="100%"
                        />
                    </div>
                    <div className={Estpa1.imgtop}>
                    <img src={asado}
                        width="100%"
                        height="100%"
                        />
                    </div>
                    <div className={Estpa1.imgbot}>
                    <img src={chuima}
                        width="100%"
                        height="100%"
                        />
                    </div>
                    <div className={Estpa1.desc}>
                    <img src={pique}
                        width="100%"
                        height="100%"
                        />
                    </div>
                </div>
              
                <form/>
            </div>    
        </body>
    </div>
    );
}