import Estpa6 from './pagina6.module.css'
import salteña1 from '../assets/img/salteña1.jpeg'
import salteña2 from '../assets/img/salteña2.jpg'
import salteña3 from '../assets/img/salteña3.jpg'
import salteña4 from '../assets/img/salteña4.jpg'
import salteña5 from '../assets/img/salteña5.jpg'
import salteña6 from '../assets/img/salteña6.jpg'
import salteña7 from '../assets/img/salteña7.jpg'

import Encabezado from "../componentes/encabezado";
export const Pagina6 =() => {
    return (
        <div className={Estpa6.Pag6}>
            <Encabezado titulo="LA SALTEÑA"/>
            <body className={Estpa6.cuerpo}>
                
                <div className={Estpa6.imagenes}>
                    <div className={Estpa6.imagen}>
                    <img src={salteña1}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={salteña2}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={salteña3}
                        width="100%"
                        height="100%"/>
                    </div>
                </div>
                <div className={Estpa6.contsec}>
                    <section className={Estpa6.seccion}>
                    <img src={salteña4}
                        width="100%"
                        height="100%"/>
                    </section>
                </div>
                <div className={Estpa6.imagenes}>
                    <div className={Estpa6.imagen}>
                    <img src={salteña5}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={salteña6}
                        width="100%"
                        height="100%"/>
                    </div>
                    <div className={Estpa6.imagen}>
                    <img src={salteña7}
                        width="100%"
                        height="100%"/>
                    </div>
                </div>
            </body>
        </div>
        
    );
}